<template>
<div :id="id" class="vue-model mod-search">
    <div class="frontend">
        <header :class="{'search-filter':data.isSearchFilter}" :style="{background:false == data.isSearchFilter ? data.headerStyle.bgStyle : ''}">
            <div class="search-left" v-show="data.isSearchLeft">
                <span>上海 <i class="iconfont icon-down-arrow"></i></span>
            </div>
            <div class="search-center">
                <i class="iconfont icon-search"></i> {{data.text}}
            </div>
            <div class="search-right" v-show="data.isSearchRight">
                <em></em>
                <i class="iconfont icon-message"></i>
            </div>
        </header>
    </div>
    <div class="backend" v-show="setting">
        <div class="arrow"></div>
        <form class="form-horizontal">
            <div class="form-group">
                <label for="inputTitle" class="col-sm-3 control-label"><span class="text-danger">*</span> 标题：</label>
                <div class="col-sm-7">
                    <input type="text" class="form-control title-name" v-model="data.text">
                </div>
            </div>
            <div class="form-group">
                <label for="inputTitle" class="col-sm-3 control-label"><span class="text-danger">*</span> 是否悬浮：</label>
                <div class="col-sm-7">
                <label class="radio-inline" v-for="item in data.isFixed">
                    <input type="radio" name="isFixed" :value="item.key" v-model="data.isFixedSel"  v-cloak> {{ item.text}}
                </label>
                </div>
            </div>
            <div class="form-group" :class="{'fixed-color':data.isSearchFilter}">
                <label class="col-sm-3 control-label">背景颜色：</label>
                <div class="col-sm-7">
                    <input type="color" class="form-control" v-model="data.headerStyle.bgStyle" />
                </div>
            </div>


            <div class="form-group">
                <label for="inputTitle" class="col-sm-3 control-label"><span class="text-danger">*</span> 定位：</label>
                <div class="col-sm-7">
                <label class="radio-inline" v-for="item in data.isLocation">
                    <input type="radio" name="isLocation" :value="item.key" v-model="data.isLocationSel"  v-cloak> {{ item.text}}
                </label>
                </div>
            </div>
            <div class="form-group">
                <label for="inputTitle" class="col-sm-3 control-label"><span class="text-danger">*</span> 消息：</label>
                <div class="col-sm-7">
                <label class="radio-inline" v-for="item in data.isMessage">
                    <input type="radio" name="isMessage" :value="item.key" v-model="data.isMessageSel"  v-cloak> {{ item.text}}
                </label>
                </div>
            </div>
        </form>
    </div>
</div>
</template>

<script type="text/javascript">
import $ from 'jquery';
export default {
    name: 'search',
    props: ['id', 'data', 'setting'],
    data() {
        return {
            test: 'headerss'
        }
    },
    created(){
      $(function(){
        document.addEventListener('touchstart mousedown', this.touchX, false);
        document.addEventListener('touchmove mousemove', this.touchX, false);
        document.addEventListener('touchend mouseup', this.touchX, false);
      })
    },
    mounted() {

    },
    methods: {
        touchX(event) {
            // let evt = event || window.event;
            console.log(1);
        }

    },
    watch:{
      "data.isFixedSel":{
          handler(val,oldVal){
          switch (val) {
              case 1:
                  this.data.isSearchFilter = false;
                  break;
              default:
                  this.data.isSearchFilter = true;
                  break;
          }
          }
      },
      "data.isLocationSel":{
        handler(val,oldVal){
          switch (val) {
              case 1:
                  this.data.isSearchLeft = false;
                  break;
              default:
                  this.data.isSearchLeft = true;
                  break;
          }
        }
      },
      "data.isMessageSel":{
        handler(val,oldVal){
          switch (val) {
              case 1:
                  this.data.isSearchRight = false;
                  break;
              default:
                  this.data.isSearchRight = true;
                  break;
          }
        }
      }
    }
}
</script>

<style>
.mod-search header {
    display: flex;
    align-items: center;
    -webkit-justify-content: center;
    justify-content: center;
    background: #ec5151;
    width: 100%;
    height: 4.4rem;
    color: #fff;
    padding:0 1rem
}
.mod-search .fixed-color{
  display:none
}
.mod-search .search-left{
      padding: 0 1rem 0 0;
}
.mod-search .search-right{
      padding: 0 0 0 1rem;
}
.mod-search .search-left,
.mod-search .search-right {
    justify-content: center;
}

.mod-search .search-left i {
    font-size: 1rem
}

.mod-search .search-center {
    -webkit-flex: 1;
    flex: 1;
    height: 2.8rem;
    line-height: 2.8rem;
    padding: 0 1rem;
    box-sizing: content-box;
    font-size: 1.5rem;
    border-radius: .4rem;
    background: #fff;
    color: #999;
}

.mod-search .search-center i.icon-search {
    margin-right: .4rem;
}

.mod-search .search-right {
    position: relative;
    -webkit-justify-content: center;
    justify-content: center;
}

.mod-search .search-right i {
    -webkit-flex: 1;
    flex: 1;
    font-size: 2.2rem;
}

.mod-search .search-right em {
    position: absolute;
    display: block;
    width: .5rem;
    height: .5rem;
    border-radius: 100%;
    background: #ff0000;
    right: 0;
    top: .1rem;
    border: 1px solid #fff;
}

header.search-filter {
    position: absolute;
    z-index: 11;
    width: 320px;
    background: none;
    background-image: linear-gradient( rgba(0, 0, 0, 0.7) 0%, rgba(0, 0, 0, 0.6) 60%, rgba(0, 0, 0, 0) 98%);
}

header.search-filter .search-center {
    background: rgba(255, 255, 255, 0.8);
    color: #666;
}
</style>



